.location-links {
  margin-top: 22px; }
  .location-links__list {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 14px; }
    .location-links__list li {
      margin-bottom: 8px; }
  .location-links__title {
    font-size: 16px;
    padding-bottom: 6px;
    margin-bottom: 8px;
    border-bottom: 1px solid #cacaca; }
  @media screen and (min-width: 48em) {
    .location-links {
      margin-top: 30px; }
      .location-links__list {
        column-count: 2;
        margin-bottom: 22px; }
        .location-links__list li {
          margin-bottom: 6px; }
      .location-links__title {
        font-size: 21px;
        padding-bottom: 8px;
        margin-bottom: 18px; } }
